const sexArray = ['保密' , '男' , '女'];
const emotionArray = ['保密' , '未婚' , '已婚'];
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';
import mpvueCityPicker from '@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue';
export default {
	components:{
		uniListItem,
		mpvueCityPicker
	},
	data() {
		return {
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '',
			
			userpic:"/static/default.png",
			username:"昵称",
			sex:0,
			emotion:0,
			job:"保密",
			birthday:"1999-09-09"
		}
	},
	// 监听返回
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
	  	this.$refs.mpvueCityPicker.pickerCancel();
	    return true;
	  }
	},
	// 监听页面卸载
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	computed: {
		sexText() {
			return sexArray[this.sex];
		},
		emotionText() {
			return emotionArray[this.emotion];
		}
	},
	methods: {
		showCityPicker(){
			this.$refs.mpvueCityPicker.show()
		},
		// 三级联动提交事件
		onConfirm(e) {
			this.pickerText = e.label
		},
		// 修改生日
		onDateChange(e){
			this.birthday = e.detail.value
		},
		// 修改头像
		changeUserpic(){
			uni.chooseImage({
				count:1,
				sizeType:["compressed"],
				sourceType:["album","camera"],
				success: (res) => {
					this.userpic = res.tempFilePaths[0]
				}
			})
		},
		// 修改性别
		changeSex(){
			uni.showActionSheet({
			    itemList: sexArray,
			    success: (res) =>{
			        this.sex = res.tapIndex
			    },
			});
		},
		// 修改情感
		changeEmotion(){
			uni.showActionSheet({
			    itemList: emotionArray,
			    success: (res) =>{
			        this.emotion = res.tapIndex
			    },
			});
		},
		// 修改职业
		changeJob(){
			let JobArray = ['IT','教师','公务员']
			uni.showActionSheet({
			    itemList: JobArray,
			    success: (res) =>{
			        this.job = JobArray[res.tapIndex]
			    },
			});
		}
	}
}
